<template>
  <div id="add-blog">
      <h2>添加博客</h2>
      <form v-if="!summited">
          <label>标题</label>
          <input type="text" v-model="blog.title" required>

          <label>内容</label>
          <textarea name="" id="" cols="30" rows="10" v-model="blog.content"></textarea>
          <label>博客类型</label>
        <div id="checkbox">
          
          <input type="checkbox" value="Javascript" v-model="blog.categories">
          <label>Javascript</label>
          <input type="checkbox" value="Jquery" v-model="blog.categories">
          <label>Jquery</label>
          <input type="checkbox" value="CSS" v-model="blog.categories">
          <label>CSS</label>
          <input type="checkbox" value="HTML" v-model="blog.categories">
          <label>HTML</label>
          <input type="checkbox" value="Vue.js" v-model="blog.categories">
          <label>Vue.js</label>
        </div>
          <label>作者</label>
          <select v-model="blog.author">
              <option v-for="author in authors" :key="author">
                  {{author}}
              </option>
          </select>
      </form>
      <button v-on:click.prevent = "post">添加博客</button>
      <hr>
      <div id="preview" v-if="summited">
          <h2>博客预览</h2>
          <p>博客标题:{{blog.title}}</p>
          <p>作者:{{blog.author}}</p>
          <p>博客类型</p>
          <ul>
              <li v-for="category in blog.categories" :key="category">
                  {{category}}
              </li>
          </ul>
          
          <p>博客内容</p>
          <p>{{blog.content}}</p>
      </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
      blog : {
          title : "",
          content : "",
          categories : [],
          author : "wuli"
      },
      authors : ["wuli","xiang"],
      summited : false
    }
  },
  methods:{
      post:function(){
          this.$http.post("https://wd0738399414hahtqn.wilddogio.com/posts.json",this.blog)
            .then(function(data){
                console.log(data);
                this.summited = true;
            });        
      }
  }
}
</script>
<style scoped>
#add-blog *{
    box-sizing: border-box;
}
#add-blog{
    margin:20px auto;
    max-width: 600px;
    padding: 20px;
}
label{
    display: block;
    margin:20px 0 10px;
}
input[type = "text"],textarea,select{
    display:block;
    width:100%;
    margin-top: 8px;
}
textarea{
    height:400px;
}
#checkbox label{
    display: inline-block;
    margin-top: 0;
    margin-right: 10px;
}
#checkbox input{
    display: inline-block;

}
button{
    display: block;
    margin: 20px auto;
    background: #000;
    color:#fff;
    border:0;
    padding: 14px;
    border-radius: 4px;
    font-size: 18px;
    cursor:pointer;
}
#preview{
    padding: 10px 20px;
    border: 1px dotted #ccc;
    margin: 30px 0; 
}
h3{
    margin-top: 10px;
}
</style>




